.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.chat-row-discription {
  font-size: smaller;
  max-height: 4.5em;
  color: gray;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: wrap;
  margin: 0.3em 0;
}

.w-30c {
  width: 20rem;
  min-width: 20rem !important;
}
